import React, { Component, Fragment } from 'react';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import Grid from 'material-ui/Grid';
import TextField from 'material-ui/TextField';
import { withStyles } from 'material-ui/styles';
import Paper from 'material-ui/Paper';
import Divider from 'material-ui/Divider';
import { FlipCard } from 'react-flop-card';
import * as _ from "lodash";
import pink from 'material-ui/colors/pink';

const styles = theme => ({
  emailField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 'calc(100% - 100px)'
  },
  top100Margin: {
    marginTop: '100px'
  },
  brandContainer: {
    height: '80px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  descriptionContainer: {
    textAlign: 'right'
  },
  flipImage: {
    width: "100%",
    height: "190px",
    padding: "5px"
  },
  frontCard: {
    width: "100%",
    height: "100%",
    backgroundColor: pink[400],
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  },
  backCard: {
    width: "100%",
    height: "100%",
    backgroundColor: pink[800],
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
  }
});


class Home extends Component {

  constructor(){
    super();
    this.state = {
      flippedImage: null
    }
  }

  brandsDOM = () => {
    const {classes} = this.props;
    return _.map(_.range(1, 6), i => {
      return (
        <Grid key={i} item md={2}>
          <Paper className={classes.brandContainer}>Brand {i}</Paper>
        </Grid>
      );
    });
  };

  flipImagesDOM = () => {
    const { classes } = this.props;
    return _.map(_.range(1, 7), i => {
        return <Grid key={i} item md={4} className={classes.flipImage}>
          <FlipCard width={180} height={180} flipped={this.state.flippedImage === i}
            onMouseOver={() => this.setState({ flippedImage : i })}
            onMouseOut={() => this.setState({ flippedImage : null})}
            frontChild={<div className={classes.frontCard} >Front {i}</div>} 
            backChild={<div className={classes.backCard}>Back {i}</div>} />
        </Grid>
    });
  }

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Grid container spacing={24}>
          <Grid item md={2} />
          <Grid item md={4}>
            <Typography
              variant="display2"
              gutterBottom
              className={classes.top100Margin}
            >
              Girl,<br />
              we’ve got you.
            </Typography>
            <Typography variant="title" gutterBottom>
              India’s first Rural + Urban Internship<br />
              community for every girl, woman and teacher.
            </Typography>
            <div style={{ paddingTop: 50 }}>
              <TextField
                label="Enter your Email id"
                className={classes.emailField}
              />
              <Button variant="raised" color="primary" mini size="small">
                Go
              </Button>
            </div>
          </Grid>
          <Grid item md={4} />
          <Grid item md={2} />
        </Grid>
        <Grid container spacing={24} className={classes.top100Margin}>
          <Grid item md={12} style={{ textAlign: 'center' }}>
            <Typography variant="subheading" gutterBottom>
              Projects offered by
            </Typography>
          </Grid>
          <Grid item md={1} />
          {this.brandsDOM()}
          <Grid item md={1} />
          <Grid item md={12}>
            <Divider />
          </Grid>
        </Grid>
        <Grid container spacing={24} className={classes.top100Margin}>
          <Grid item md={1} />
          <Grid item md={5}>
            <Grid container spacing={8}>
              {this.flipImagesDOM()}
            </Grid>
          </Grid>
          <Grid item md={1} />
          <Grid item md={4} className={classes.descriptionContainer}>
            <Typography variant="title">Truly affective.</Typography>
            <br />
            <br />
            TrulyHers is actively reaching out to girls in the remotest parts of
            the country every single day. We offer beautifully designed
            internship projects created with some of the biggest brands in
            India, covering crucial topics such as{' '}
            <b>
              {' '}
              personal care, hygiene, self awareness, sex education, acceptance{' '}
            </b>
            and all things <b> future building.</b>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}
export default withStyles(styles)(Home);



