import { createMuiTheme } from 'material-ui/styles';
import pink from 'material-ui/colors/pink';
const theme = createMuiTheme({
  palette: {
    primary: {
      light: pink[200],
      main: pink[300],
      dark: pink[500],
      contrastText: '#fff'
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000'
    }
  }
});
export default theme;



